.chatbot-container {
    height: 100%;
    width: 100%;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    padding: 0 10px;
    box-sizing: border-box;
    // height: 95vh;
    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
    // display: flex;
    // flex-direction: column;
    // justify-content: center;
    // // align-items: center;
    // height: 95vh;
    // font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    // 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    // sans-serif;
} 
.messages {
    display: flex;
    flex-direction: column;
    height: 80%;
    overflow-y: auto;
    padding: 10px;
    border-radius: 10px;
    margin-bottom: 5px;
} 

&.user {
    align-self: flex-end;
    background-color: lightskyblue;
}
  
&.bot {
    align-self: flex-start;
    background-color: lightgray;
}

  
.input-container {
    display: flex;
    align-items: center;
    height: 10%;
} 

.input {
    flex: 1;
    padding: 10px;
    border-radius: 10px;
    border: 1px solid lightgray;
    margin-right: 10px;
}
  
.send-button {
    padding: 10px;
    border-radius: 10px;
    background-color: lightskyblue;
    border: none;
    cursor: pointer;
}
